<template>
  <div class="posts-wrap">
    <!--<forum-header-->
      <!--:labels="labels"-->
    <!--&gt;</forum-header>-->
    <forum-new-header
      :labels="labels"
    >
    </forum-new-header>
    <div id="posts" class="posts-main">
      <div class="head">
        <div class="infos">
          <h1>{{posts.title}}</h1>
          <div class="info">
            <router-link :to="'/forum/home/' + posts.user_id">{{posts.user_name}}</router-link>
            <img
              v-for="(item, index) in posts.medal"
              :key="index"
              :src="item.imgurl"
              :title="item.name"
              :alt="item.content"
              style="cursor: pointer; width: 22px;height: 33.25px;vertical-align: middle;"
            >
            发表于 <abbr title>{{posts.create_time}}</abbr>
            最后回复 <abbr>{{posts.last_reply_time}}</abbr>
            -{{posts.browse}}次浏览
            <a href="javascript:void(0)" @click="report(1)">举报</a>
          </div>
        </div>
        <div class="img">
          <img :src="posts.headimgurl" alt="">
        </div>
      </div>
      <posts-manage
        v-show="posts.moderators"
        :posts="posts"
        :labels="labels"
      ></posts-manage>
      <div class="like-btn">
        <a href="javascript:void(0)" v-on:touchend="clickZanFn">
          <i class="fa fa-thumbs-up" aria-hidden="true"></i>
          赞({{zan}})
        </a>
        <a href="javascript:void(0)" v-on:touchend="clickCollection">
          <i class="fa fa-star" aria-hidden="true"></i>
          收藏
        </a>
      </div>
      <div class="main">
        <div class="content" v-html="posts.content"></div>
        <div class="reply">
          <reply-list
            :replyList="replyList"
            :reply_amount="posts.reply_amount"
          ></reply-list>
          <div class="loadmore" v-show="load">
            <mt-button plain @click="loadMore">加载更多</mt-button>
          </div>
        </div>
        <div class="commit">
          <div class="floor" v-show="showFloor">
            对 <a href="javascript:void(0)">@{{floorName}}</a>回复
            <a href="javascript:void(0)"
               v-on:touchend="cancelReply"
               style="color: red; font-size: 18px">x</a>
          </div>
          <div id="editor" class="editor" contenteditable="true"></div>
          <div class="clearfix btn">
            <div class="uploader-box" style="width: 3rem; height: 3rem;">
              <input type="file" ref="resource" id="file" v-on:change="uploadFile">
            </div>
            <a href="javascript:void(0)" class="send-btn" @click="sendCommit"><strong>发表评论</strong></a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import ForumHeader from '../forum-header.vue'
import ForumNewHeader from '../forum-new-header.vue'
import ReplyList from './posts-reply.vue'
import PostsManage from './posts-manage.vue'
import bus from '../../../assets/js/bus'
import {getLocalStorage, setPageTitle} from '../../../lib/util'
import {getPostsInfo, upload, sendPostsReply, clickZan, clickCollectBtnByPosts, forumAccusation} from '../../../api/forum'
import { Toast } from 'mint-ui'
export default {
  name: 'Forum-Posts',
  data () {
    return {
      labels: [],
      replyList: [],
      posts: {},
      imgs: '',
      zan: 0,
      floorName: '',
      floorId: '',
      showFloor: false,
      page: 1,
      load: false
    }
  },
  created () {
    this.getLabels()
    this.getDatas()
  },
  watch: {
  },
  mounted () {
    bus.$on('replySomeone', reply => {
      let postsWrap = document.getElementById('posts')
      this.showFloor = true
      this.$set(this, 'floorId', reply.reply_id)
      this.$set(this, 'floorName', reply.user_name)
      postsWrap.scrollIntoView(false)
    })
    this.$on('btn-success', () => {
      this.getDatas()
    })
  },
  methods: {
    getLabels () {
      this.$set(this, 'labels', JSON.parse(getLocalStorage('labels')))
    },
    getDatas () {
      getPostsInfo(this.$route.params.id, undefined)
        .then(data => {
          if (data.state) {
            if (data.page > 12) {
              this.load = true
            }
            setPageTitle(data.data.info.title)
            this.$set(this, 'zan', data.data.info.fabulous)
            this.$set(this, 'posts', data.data.info)
            this.$set(this, 'replyList', data.data.info.reply)
          }
        })
    },
    sendCommit () {
      let userInfo = this.$store.getters.getUserInfo
      if (!userInfo.tel) {
        setTimeout(() => {
          return this.$router.push('/user/select')
        }, 1500)
      }
      let cont = document.getElementById('editor').innerHTML
      sendPostsReply(this.$route.params.id, cont, this.floorId)
        .then(data => {
          if (data.state) {
            document.getElementById('editor').innerHTML = ' '
            let postsWrap = document.getElementById('posts')
            postsWrap.scrollIntoView(true)
            this.$set(this, 'floorId', '')
            this.$set(this, 'floorName', '')
            this.$set(this, 'showFloor', false)
            this.getDatas()
          } else {
            Toast(data.errormsg || '回复失败')
          }
        })
        .catch(err => this.$toast('网络错误:' + err.message))
    },
    uploadFile () {
      let formData = new FormData()
      formData.append('0', this.$refs.resource.files[0])
      upload(formData)
        .then(data => {
          if (data.state) {
            let img = document.createElement('img')
            img.setAttribute('src', data.data.url)
            document.getElementById('editor').appendChild(img)
          } else {
            Toast(data.errormsg || '上传失败')
          }
        })
        .catch(err => console.log(err))
    },
    cancelReply () {
      this.$set(this, 'floorId', '')
      this.$set(this, 'floorName', '')
      this.$set(this, 'showFloor', false)
    },
    clickZanFn () {
      clickZan(this.$route.params.id)
        .then(data => {
          if (data.state) {
            this.$set(this, 'zan', this.zan + 1)
            Toast(data.errormsg || '点赞成功')
          } else {
            Toast(data.errormsg)
          }
        })
    },
    clickCollection () {
      clickCollectBtnByPosts(this.$route.params.id)
        .then(data => {
          if (data.state) {
            Toast(data.errormsg || '收藏成功')
          } else {
            Toast(data.errormsg || '收藏失败')
          }
        })
    },
    report (type) {
      this.$messagebox.prompt('请输入举报原因')
        .then(({value, action}) => {
          if (!value) return this.$toast('请输入举报原因')
          forumAccusation(this.$route.params.id, type, value)
            .then(data => this.$toast(data.errormsg))
            .catch(() => this.$toast('网络错误'))
        })
        .catch(() => {
        })
    },
    loadMore () {
      this.$indicator.open({
        text: '加载中...'
      })
      this.page = this.page + 1
      getPostsInfo(this.$route.params.id, this.page)
        .then(data => {
          if (data.state) {
            this.$indicator.close()
            if (data.data.info.reply.length === 0 || data.data.info.reply.length < 12) {
              this.load = false
            }
            this.replyList.push(...data.data.info.reply)
          } else {
            this.$indicator.close()
          }
        })
    }
  },
  components: {
//    ForumHeader,
    ForumNewHeader,
    ReplyList,
    PostsManage
  }
}
</script>

<style lang="less" scoped>
  .posts-wrap{
    height: 100%;
    background-color: #e5e5e5;
    .posts-main {
      width: 100%;
      position: absolute;
      padding-top: 40px;
      overflow: hidden;
      background-color: #e5e5e5;
      &:after {
        content: " ";
        height: 5rem;
        display: block;
      }
      .head{
        padding: 1rem;
        display: flex;
        background-color: #fafafa;
        border-bottom: 1px solid #eee;
        color: #777;
        transition: all .3s;
        .infos{
          flex: 1;
          padding-right: .6rem;
          h1 {
            margin-top: 0;
            font-size: 18px;
            font-weight: 500;
            color: #333;
            text-align: left;
            line-height: 150%;
            margin-bottom: 8px;
          }
          .info{
            color: #c0c0c0;
            font-size: 12px;
            a{
              color: #356DD0;
              font-size: 13px;
            }
            abbr{
              cursor: help;
              border-bottom: 1px dotted #777;
            }
          }
        }
        .img{
          width: 3rem;
          height: 3rem;
          img{
            width: 100%;
            height: 100%;
            border-radius: 50%;
          }
        }
      }
      .like-btn{
        padding: 1rem;
        background: #fafafa;
        border-top: 1px solid #eee;
        border-bottom-right-radius: 3px;
        border-bottom-left-radius: 3px;
        a {
          font-size: 14px;
          color: #333;
          margin-right: 2rem;
        }
      }
      .main{
        padding: 1rem;
        background-color: #fff;
        .content{
          background-color: white;
          position: relative;
          letter-spacing: .03em;
          text-overflow: ellipsis;
          word-wrap: break-word;
          img{
            width: 100%;
            max-width: 100%;
          }
        }
        .reply{
          .loadmore{
            text-align: center;
          }
        }
        .commit{
          .editor{
            width: 100%;
            height: 150px;
            overflow: hidden;
            border: 1px solid #ddd;
          }
          .btn {
            .send-btn{
              margin-top: 10px;
              float: right;
              margin-right: 0;
              height: 30px;
              background: #f60;
              color: white;
              font-size: 14px;
              vertical-align: middle;
              -moz-box-shadow: 0 1px 0 #E5E5E5;
              -webkit-box-shadow: 0 1px 0 #E5E5E5;
              box-shadow: 0 1px 0 #E5E5E5;
              strong{
                line-height: 30px;
                font-weight: 700;
                padding: 0 10px;
              }
            }
          }
        }
      }
    }
  }
</style>



