<template>
  <div class="reply">
    <div class="avatar">
      <router-link  :to="'/forum/home/' + replys.user_id"><img :src="replys.headimgurl" alt="用户头像"></router-link>
    </div>
    <div class="infos">
      <div class="info">
        <router-link class="name" :to="'/forum/home/' + replys.user_id">{{replys.user_name}}</router-link>
        <img
          v-for="(item, index) in replys.medal"
          :key="index"
          :src="item.imgurl"
          :title="item.name"
          :alt="item.content"
          style="cursor: pointer; width: 22px;height: 33.25px;vertical-align: middle;"
        >
        ·
        <span class="floor">{{replys.floor_id}}楼</span>
        ·
        <span class="time">{{replys.create_time}}</span>
        <a href="javascript:void(0)" @click="report(2)">举报</a>
        <a href="javascript:void(0)"  :data-reply-id="replys.reply_id" v-on:touchend="reply" style="font-size: 12px;">回复</a>
      </div>
      <div class="isreply" v-if="replys.pid">
        <div class="info">
          对 <img :src="replys.received_headimgurl" alt=""> <a href="javascript:void(0)">{{replys.received_username}}</a> #{{replys.pid}}回复
        </div>
        <div class="cont" v-html="replys.received_content"></div>
      </div>
      <div class="reply-content" v-html="replys.content"></div>
    </div>
  </div>
</template>

<script>
import bus from '../../../assets/js/bus'
import {forumAccusation} from '../../../api/forum'
export default {
  name: 'Posts-Reply-Item',
  props: {
    replys: Object
  },
  methods: {
    reply () {
      bus.$emit('replySomeone', this.replys)
    },
    report (type) {
      this.$messagebox.prompt('请输入举报原因')
        .then(({value, action}) => {
          if (!value) return this.$toast('请输入举报原因')
          forumAccusation(this.replys.reply_id, type, value)
            .then(data => this.$toast(data.errormsg))
            .catch(() => this.$toast('网络错误'))
        })
        .catch(() => {
        })
    }
  }
}
</script>

<style lang="less" scoped>
  .reply {
    padding: 15px 0;
    position: relative;
    border-bottom: 1px solid #eee;
    display: flex;
    .avatar{
      width: 48px;
      height: 48px;
      margin-right: 15px;
      a{
        display: block;
        img {
          width: 100%;
          height: 100%;
          border-radius: 50%;
        }
      }
    }
    .infos {
      flex: 1;
      min-height: 48px;
      .info {
        font-size: 12px;
        margin-bottom: 6px;
        color: #999;
        .name {
          font-weight: bold;
          font-size: 13px;
          color: #555;
        }
        .floor{
          color: #7aa87a;
        }
        .time{
          color: #999;
          border-bottom: 1px dashed #ccc;
          text-decoration: none !important;
          cursor: pointer;
        }
      }
      .isreply {
        padding: 8px 15px;
        background: #f7f7f7;
        border-radius: 3px;
        margin-bottom: 15px;
        .info {
          color: #999;
          font-size: 12px;
          img{
            width: 16px;
            height: 16px;
            border-radius: 50%;
          }
          a{
            color: #666;
          }
        }
      }
    }
    .icon {
      cursor: pointer;
      i {
        font-size: 20px !important;
      }
    }
  }
</style>



