<template>
  <div class="btns">
    <a href="javascript:void(0)" @click="passOrReject(1)" v-show="posts.apply">通过</a>
    <a href="javascript:void(0)" @click="passOrReject(0)" v-show="posts.apply">拒绝</a>
    <a href="javascript:void(0)" @click="clickTopBtn(1)" v-if="!posts.top">置顶</a>
    <a href="javascript:void(0)" @click="clickTopBtn(0)" v-else>取消置顶</a>
    <a href="javascript:void(0)" @click="deletePosts">删除</a>
    <a href="javascript:void(0)" @click="setGood(0)" v-if="posts.highlight">取消加精</a>
    <a href="javascript:void(0)" @click="setGood(1)" v-else>加精</a>
    <select v-model="selectLabel">
      <option
        v-for="(item, index) in labels"
        :key="index"
        :value="item.label_id"
      >{{item.cn}}</option>
    </select>
    <a href="javascript:void(0)" @click="movePost">转移</a>
  </div>
</template>

<script>
import {operateForumPostsTop, deleteForumPosts, operateForumPostsGood, movePostsToLabel, forumPostsPassOrReject} from '../../../api/forum'
export default {
  name: 'Posts-Manage',
  props: {
    posts: Object,
    labels: ''
  },
  data () {
    return {
      selectLabel: ''
    }
  },
  methods: {
    clickTopBtn (v) {
      operateForumPostsTop(this.$route.params.id, v)
        .then(data => {
          if (data.state) {
            this.$toast(data.errormsg)
            this.$parent.$emit('btn-success')
          } else {
            this.$toast(data.errormsg)
          }
        })
    },
    deletePosts () {
      deleteForumPosts(this.$route.params.id)
        .then(data => {
          if (data.state) {
            this.$toast(data.errormsg)
            this.$router.back()
          } else {
            this.$toast(data.errormsg)
          }
        })
    },
    setGood (v) {
      operateForumPostsGood(this.$route.params.id, v)
        .then(data => {
          if (data.state) {
            this.$toast(data.errormsg)
            this.$parent.$emit('btn-success')
          } else {
            this.$toast(data.errormsg)
          }
        })
    },
    movePost () {
      movePostsToLabel(this.$route.params.id, this.selectLabel)
        .then(data => {
          if (data.state) {
            this.$toast('操作成功')
            this.$router.replace('/forum/list/' + this.selectLabel)
          } else {
            this.$toast(data.errormsg || '操作失败')
          }
        })
    },
    passOrReject (v) {
      if (v === 1) {
        forumPostsPassOrReject(this.$route.params.id, v)
          .then(data => {
            this.$toast(data.errormsg)
            this.$parent.$emit('btn-success')
          })
      } else {
        this.$messagebox.prompt('请输入拒绝原因')
          .then(({value, action}) => {
            forumPostsPassOrReject(this.$route.params.id, v, value)
              .then(data => {
                this.$toast(data.errormsg)
                this.$router.back()
                this.$parent.$emit('btn-success')
              })
          }).catch(this.$toast('未操作'))
      }
    }
  }
}
</script>

<style lang="less" scoped>
  .btns{
    display: flex;
    background-color: #fafafa;
    a{
      flex: 1;
      text-align: center;
      color: #707274;
    }
  }
</style>



