<template>
  <div class="reply-wrap">
    <div class="title">共{{reply_amount}}条回复</div>
    <div class="reply-list" id="reply-content">
      <posts-reply-item
        v-for="(item, index) in replyList"
        :key="index"
        :replys="item"
      ></posts-reply-item>
      <div v-show="loadmore">
        加载中。。。
      </div>
    </div>
  </div>
</template>

<script>
import PostsReplyItem from './posts-reply-item.vue'
export default {
  name: 'Posts-Reply',
  props: {
    replyList: '',
    reply_amount: 0
  },
  data () {
    return {
      loadmore: false
    }
  },
  methods: {
    loadBottom () {
    }
  },
  components: {
    PostsReplyItem
  }
}
</script>

<style lang="less" scoped>
  .reply-wrap{
    margin-bottom: 15px;
    border-radius: 3px;
    border: 1px;
    border-color: #e5e6e9 #dfe0e4 #d0d1d5;
    box-shadow: 0 1px 1px rgba(0,0,0,0.05);
    background-color: white;
    .title{
      background: #fafafa;
      padding: 6px 15px;
      border-bottom-color: #eee;
      color: #777;
      border-color: #ddd;
      border-bottom: 1px solid transparent;
      border-top-left-radius: 3px;
      border-top-right-radius: 3px;
    }
  }
</style>



